/* IMPORTS */

/* GENERAL */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img,
video {
  max-width: 100%;
}

/* FLOAT CLEARFIX */
.group::after {
  content: "";
  display: table;
  clear: both;
}

/* PAGE-HEADER */

.page-header {
  background: #12b0ee;
  padding: 50px 0 0;
  color: #fff;
  min-width: 320px;
  font-family: "Roboto", sans-serif;
}

.page-header-content {
  width: 95%;
  margin: 80px auto;
  padding: 10px 20px;
  text-align: center;
}

.page-header-content .heading {
  margin: 20px 0 40px;
}

.page-header-content .heading h1 {
  font-size: 40px;
  font-weight: 500;
}

.page-header-content .heading p {
  font-size: 16px;
}

.page-header-content p.info {
  font-size: 1.2rem;
  line-height: 1.7rem;
  /* font-size: 16px;
    line-height: 1.6em; */
}

.page-header-content .header-asset {
  margin: 50px 0;
}

/* CONTAINER */
.container {
  max-width: 95%;
  margin: 0px auto;
  text-align: center;
  font-family: "Roboto", sans-serif;
}

/* WHATSAPP INFO */

.whatsapp-info {
  margin: 20px 0 50px;
}

.whatsapp-info > h1 {
  font-size: 32px;
  color: #333;
  font-weight: 500;
  margin: 30px 0 0;
}

/* INFO TILE */
.info-tile {
  margin: 30px 0;
}

.info-tile h1 {
  font-size: 54px;
  font-weight: 500;
  color: #222;
}

.info-tile p {
  font-size: 19px;
  color: #666;
}

.info-tile .tile-icon {
  width: 20%;
  min-width: 70px;
  max-width: 100px;
  margin: 20px 0 -10px;
}

/* WHATSAPP CHATBOT */

.whatsapp-chatbot-container {
  background: #fafbff;
}

.whatsapp-chatbot {
  margin: 50px 0;
  margin: 0 auto;
  padding: 50px 0;
  padding: 10px;
}

.whatsapp-chatbot-container .whatsapp-chatbot h2 {
  font-size: 32px;
  font-weight: 500;
  color: #333;
}

.whatsapp-chatbot-container .whatsapp-chatbot p {
  font-size: 1.1rem;
  line-height: 1.5rem;
  margin: 50px 0;
}

.whatsapp-chatbot-container .whatsapp-chatbot .info-img {
  width: 95%;
  margin: 20px auto;
}

.info-content {
  margin: 50px 0 0;
}

.info-content h1 {
  font-size: 20px;
  font-weight: 500;
  margin: 20px 0;
}

/* FEATURES */

.features h2 {
  font-size: 32px;
  font-weight: 500;
  margin: 50px 0;
}

.feature-tile {
  margin: 50px 0;
}

.feature-tile .feature-icon {
  width: 20%;
  max-width: 100px;
  min-width: 70px;
  margin: 20px auto;
}

.feature-tile h5 {
  font-size: 20px;
  font-weight: 500;
  margin: 8px 0 10px;
}

/* RICH EXPERIENCE */

.experience > h2 {
  font-size: 32px;
  font-weight: 500;
  color: #333;
  margin: 50px 0;
}

.experience-info {
  background: #eff3f6;
  padding: 0 20px;
  border-radius: 30px;
  margin: 100px 0 0;
  min-width: 300px;
}

.experience-info .content p {
  font-size: 1.1rem;
  line-height: 1.6rem;
  font-weight: 350px;
  /* font-size: 16px;
    font-weight: 350px; */
}

.header-desc {
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 30px;
}

.experience-info .img {
  margin: 50px 0;
}

.experience-info .experience-header-num {
  font-size: 117px;
  color: #cdd7e1;
  font-weight: bold;
  margin-top: -80px;
}

h3.many-more {
  font-size: 50px;
  font-weight: 500;
  color: #ced6e1;
  margin-bottom: 30px;
}

/* MEDIA QUERIES */
@media screen and (min-width: 576px) {
  /* EXPERIENCE */

  .experience-info {
    height: 450px;
    max-width: 500px;
    margin: 100px auto 0;
  }

  .experience-info .content {
    width: 36%;
    float: left;
    text-align: left;
  }

  .experience-info .content p {
    margin: 0 0 50px;
  }

  .experience-info .img {
    width: 60%;
    float: right;
    margin-top: -45px;
  }
}

@media screen and (min-width: 768px) {
  /* EXPERIENCE */
  .experience-info {
    height: 400px;
    max-width: 650px;
    margin: 100px auto 0px;
  }

  .experience-info .content {
    width: 45%;
    float: left;
    text-align: left;
  }

  .experience-info .content p {
    margin: 0 0 50px;
  }

  .experience-info .img {
    width: 45%;
    float: right;
    margin-top: -70px;
  }
}

@media screen and (min-width: 1024px) {
  /* PAGE HEADER */
  .page-header-content {
    width: 90%;
  }

  .page-header-content .header-asset {
    width: 40%;
    max-width: 400px;
    float: right;
  }

  .page-header-content .description {
    width: 40%;
    float: left;
    margin: 10% 50px 0;
    text-align: left;
  }

  /* WHATSAPP INFO */
  .whatsapp-info {
    max-width: 1200px;
    margin: 0px auto;
  }

  .whatsapp-info .info-tile {
    width: 23%;
    margin: 50px 1%;
    float: left;
  }

  /* FEATURES */
  .features {
    width: 95%;
    max-width: 1100;
    margin: 0 auto;
  }

  .feature-tiles {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }

  .feature-tile {
    width: 30%;
    margin-right: 3%;
    text-align: center;
    float: left;
  }

  /* CHAT BOT */

  .chatbot-info {
    margin: 50px 0;
  }

  .chatbot-info .info-img {
    width: 30%;
    max-width: 350px;
    float: left;
  }

  .chatbot-info .info-content {
    width: 50%;
    text-align: left;
    float: right;
    margin: 0 30px 0 0;
  }

  .chatbot-info:nth-child(2) .info-img {
    margin-left: 60px;
  }

  .chatbot-info:nth-child(3) .info-content {
    float: left;
    margin-left: 40px;
  }

  .chatbot-info:nth-child(3) .info-img {
    float: right;
    margin-right: 40px;
  }
}

@media screen and (min-width: 1280px) {
  /* PAGE HEADER */
  .page-header-content {
    width: 80%;
  }

  /* CONTAINER */
  .container {
    width: 90%;
  }

  /* FEATURES */
  .feature-tiles {
    width: 95%;
    max-width: 1200;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }

  /* EXPERIENCE */
  .experience-info {
    width: 45%;
    max-width: 800px;
    min-width: 400px;
    float: left;
    margin: 40px 2.5% 90px;
  }

  .experience-info .img {
    max-width: 65%;
  }

  .experience-info .img {
    margin-top: -10px;
  }
}

@media screen and (min-width: 1400px) {
  /* EXPERIENCE */
  .experience-info .img {
    margin-top: -40px;
  }
}

@media screen and (min-width: 1600px) {
  /* CONTAINER */
  .container {
    width: 80%;
  }

  /* PAGE HEADER */
  .page-header-content {
    width: 70%;
  }

  /* CHATBOT */
  .chatbot-info {
    width: 90%;
    margin: 50px auto 0;
  }

  /* EXPERIENCE */
  .experience {
    width: 90%;
    margin: 0 auto;
  }

  .experience-info .content {
    margin-left: 2%;
  }

  .experience-info .img {
    margin-top: -30px;
  }
}
